import React from 'react'
import "../App.css";
import { Container, Row, Col } from "react-bootstrap";
import gen1 from "../ima/primera_gen1.jpg";
import eniac1 from "../ima/primera_gen1_eniac.jpg";

export const Gen1 = () => {
    return (
        
        <div className="cajaGen">
            <h1>PRIMERA GENERACIN 1940 - 1956</h1>
            <p>
                La historia de las generaciones de computadoras, inicia con la invencin del primer computador. Su autora no puede atribuirse a una nica persona. Si bien, Babbage es conocido como el padre de las primeras ramas para los saberes de la computacin; no fue sino hasta aos despus que tuvimos las primeras mquinas propiamente dichas.
                En el proceso, tambin influyeron importantes figuras como Alan Turing con su Mquina de Turing o Mquina universal. Al final, la mayor caracterstica de los primeros computadores era la capacidad de hacer clculos de forma automtica y digital, por la necesidad de resolver cdigos secretos enemigos durante la segunda guerra mundial.
            </p>
            <Container>
                <Row>
                    <Col xs={12} md={6} xl={7} center>
                        <h2>Caractersticas</h2>
                        <ul>
                            <li>Necesitaban el uso de tarjetas perforadas para ingresar datos y realizar la programacin. Dichas tarjetas perforadas eran la manera de indicar las instrucciones en cdigo binario (lenguaje de mquina) al equipo.</li>
                            <li>Los primeros equipos informticos eran tan grandes que abarcaban varios metros cbicos. Estos se valan de tubos al vaco para los circuitos y tambores magnticos para la memoria.</li>
                            <li>Estas generaciones de las computadoras eran programadas en lenguaje de mquina, el lenguaje de ms bajo nivel. Al ser algo nuevo, supona un desafo para los primeros usuarios en operarla.</li>
                            <li>Muchas de sus instrucciones, muy simples y especficas, eran suministradas a travs de tarjetas perforadas de papel o cartn. Mientras la salida era mostrada en impresiones.</li>
                            <li>Solo podan resolver un problema a la vez, y a los operadores muchas veces les tomaba das o hasta semanas, establecer un nuevo problema.</li>
                        </ul>
                    </Col>
                    <Col xs={12} md={6} xl={5} center>
                        <img src={gen1} alt="G1" /><br /><br />
                        <img src={eniac1} alt="G1 ENIAC" />
                    </Col>
                </Row>
            </Container>
            <Container >
                    <div className="ratio ratio-16x9 ">
                        <iframe className="video"
                        src="https://www.youtube.com/embed/AuMVvq-h5Mo?rel=0"
                            title="YouTube video"
                            allowfullscreen
                        ></iframe>
                    </div>
            </Container>
        </div>
    )
}

